.app-container {
  display: flex;
  flex-direction: column; /* 如果您希望子元素垂直堆叠 */
  height: 100vh;
  overflow: hidden; /* 避免在app容器级别出现滚动 */
  background-color: #fff;
}

#Navigation {
  display: flex; /* 设定为弹性盒子 */
  flex-direction: column; /* 子元素垂直排列 */
  gap: 20px; /* 子元素之间间隔20px，这个属性在现代浏览器中得到支持 */
  /* margin-top: 63px; */
}
#NavigationCard {
  display: flex; /* 设定为弹性盒子 */
  flex-direction: column; /* 子元素垂直排列 */
  gap: 20px; /* 子元素之间间隔20px，这个属性在现代浏览器中得到支持 */
}
#NavigationItem {
  border: 2px solid lightgray; /* 设置边框宽度为2px，颜色为浅灰色 */

  justify-content: center; /* 水平居中子元素 */
  align-items: center; /* 垂直居中子元素 */
  border: 1px solid rgb(204, 204, 204);
  border-radius: 8px;
  box-shadow: rgba(0, 0, 0, 0.1) 2px 2px 4px;
  padding: 12px;
  /* margin: 12px 20px 12px 12px; */
}
.full-height {
  height: 100%; /* 设置Layout的高度为100% */
}
