.hide-border {
  border: none !important; /* 强制覆盖任何已有的边框样式 */
}

.custom-dropdown-menu-item {
  display: flex;
  align-items: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.custom-dropdown-menu-item span {
  flex-shrink: 0; /* 防止圆点被压缩 */
  margin-right: 8px; /* 为圆点和文本添加间隙 */
}

.custom-dropdown-menu-item div {
  flex-grow: 1; /* 允许文本占据剩余空间 */
  overflow: hidden;
  text-overflow: ellipsis;
}

.custom-select-multiple .ant-select-selection--multiple {
  overflow: auto; /* 允许滚动 */
  max-height: 100px; /* 一个合适的最大高度 */
}

/* 以下是design相关的样式 */
.custom-panel-header {
  font-weight: bold; /* 加粗字体 */
  color: #050404;
  background-color: #fff;
}

/* 覆盖 antd Panel 的默认内边距，减小高度 */
.ant-collapse > .ant-collapse-item > .ant-collapse-header {
  padding-top: 5px;
  padding-bottom: 5px;
}

/* 如果你有一个特定的类用于整个 Collapse 组件，也可以这样做 */
.custom-collapse .ant-collapse > .ant-collapse-item > .ant-collapse-header {
  padding-top: 4px;
  padding-bottom: 4px;
}

/* 调整 Panel 的内容区域内边距 */
.ant-collapse-content > .ant-collapse-content-box {
  padding: 8px;
}

.ant-collapse {
  border-radius: 0px !important;
}

.ant-collapse > .ant-collapse-item > .ant-collapse-header {
  border-radius: 0px !important;
}

.ant-collapse .ant-collapse-content {
  border-radius: 0px !important;
}

.ant-collapse .ant-collapse-content > .ant-collapse-content-box {
  border-radius: 0px !important;
}

.ant-collapse-item-collapsed > .ant-collapse-header {
  border-radius: 0px !important;
}

.ant-select .ant-select-selector {
  width: 100%; /* 确保selector占满整个Select组件的宽度 */
  overflow: hidden; /* 隐藏溢出的内容 */
  white-space: nowrap; /* 防止内容换行 */
  display: flex; /* 使用flex布局 */
  align-items: center; /* 垂直居中 */
  text-align: left; /* 文本左对齐 */
}

.ant-select .ant-select-selection-overflow {
  width: 100%; /* 确保overflow元素占满selector的宽度 */
  display: flex; /* 使子元素按行排列 */
  flex-wrap: nowrap; /* 防止子元素换行 */
  overflow: hidden; /* 隐藏溢出的内容 */
  max-width: 100%; /* 防止子元素超出容器宽度 */
  display: flex; /* 使用flex布局 */
  align-items: center; /* 垂直居中 */
  text-align: left; /* 文本左对齐 */
}

.ant-select .ant-select-selection-overflow-item {
  max-width: none; /* 移除最大宽度限制 */
  flex-grow: 1; /* 允许子项根据需要扩展 */
  margin-right: 0; /* 调整或移除外边距 */
  flex-shrink: 0; /* 防止项目在溢出容器时缩小 */
  text-align: left;
}

/* 在您的全局CSS文件中 */
.ant-picker {
  font-size: var(--dynamic-font-size) !important; /* 使用!important来确保覆盖默认样式 */
  font-weight: var(--dynamic-font-weight);
  font-style: var(--dynamic-font-style);
}

/* styles.css */
.ant-picker input {
  font-size: var(--dynamic-font-size) !important; /* 使用 CSS 变量 */
  font-weight: var(--dynamic-font-weight);
  font-style: var(--dynamic-font-style);
}

/* 在全局样式文件中 */
.ant-picker .ant-picker-input > input {
  font-weight: var(--dynamic-font-weight);
  font-style: var(--dynamic-font-style);
  /* text-decoration: var(--dynamic-text-decoration);
  color: var(--dynamic-font-color);
  background-color: var(--dynamic-background-color); */
  /* 模拟边框，实际上为内部input添加了一个外边框 */
  border: var(--dynamic-text-contour);
}

/* 更具特异性的CSS选择器来覆盖Ant Design的DatePicker样式 */
.ant-picker.ant-picker-outlined.date-picker .ant-picker-input input {
  font-weight: var(--dynamic-font-weight);
  font-style: var(--dynamic-font-style);
}

/* Add this to your CSS file */
.date-picker input {
  font-weight: var(--dynamic-font-weight);
  font-style: var(--dynamic-font-style);
}

/* Add this to your CSS file */
.date-picker {
  font-weight: var(--dynamic-font-weight);
  font-style: var(--dynamic-font-style);
}
